import React, { Component } from "react";
import "./ListItems.css";
//import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import Button from 'react-bootstrap/Button';

function ListItems(props) {
  const items = props.items;
  const listItems = items.map((item) =>
    //map will return each list item in form of div having a p tag
    {
      return (
        <div className="list" key={item.key}>
          <p>
              <input type="text" id={item.key}
                     value={item.text} 
                     onChange ={
                         (e) =>props.setUpdate(e.target.value,item.key)
                     }/>
            
            <button className="btn btn-sm delete"
                     onClick={ () => props.deleteItem(item.key)}>Delete
            </button>
          </p>
        </div>
      );
    }
  );

  return <div>{listItems}</div>;
}

export default ListItems;
